import { Container } from '@pixi/display';
import { Graphics } from '@pixi/graphics';
import { TextStyle, Text } from '@pixi/text';
import { Application } from '@pixi/app';
import { Renderer, BatchRenderer } from '@pixi/core';
import { TickerPlugin } from '@pixi/ticker';

/*
 * Package @spielhalle/sudoku-board
 * Source https://spielhalle.github.io/spielhalle/
 */
class SudokuBoardGame extends Container {
    constructor(width, height) {
        super();
        this.stage = new Container();
        this.textLayer = new Container();
        this.gameHeight = height;
        this.gameWidth = width;
        this.init();
    }
    init() {
        const grid = new Graphics().beginFill(0x00ff00);
        for (let i = 1; i < 9; i++) {
            const offset = i % 3 === 0 ? 3 : 1;
            grid.drawRect(i * 50 - offset, 0, offset * 2, 9 * 50).drawRect(0, i * 50 - offset, 9 * 50, offset * 2);
        }
        this.stage.addChild(grid);
        this.stage.addChild(this.textLayer);
        const txtStyle = new TextStyle({
            align: 'center',
            fill: 0xff1010,
            fontFamily: 'Arial',
            fontSize: 24,
        });
        for (let i = 0; i < 9 * 9; i++) {
            const text = new Text(`${i}`, txtStyle);
            text.x = 25 + (i % 9) * 50 - text.width / 2;
            text.y = 25 + Math.floor(i / 9) * 50 - text.height / 2;
            this.textLayer.addChild(text);
        }
    }
    updateText(txtItem, x, y, txt) {
        txtItem.text = txt;
        txtItem.x = 25 + x * 50 - txtItem.width / 2;
        txtItem.y = 25 + y * 50 - txtItem.height / 2;
    }
    setBoard(board) {
        let totalIdx = 0;
        for (let x = 0; x < board.length; x++) {
            for (let y = 0; y < board.length; y++) {
                this.updateText(this.textLayer.children[totalIdx], x, y, `${board[x][y]}`);
                totalIdx++;
            }
        }
    }
}

/*
 * Package @spielhalle/sudoku-board
 * Source https://spielhalle.github.io/spielhalle/
 */
Application.registerPlugin(TickerPlugin);
Renderer.registerPlugin('batch', BatchRenderer);
class SudokuBoardGameApp extends Application {
    constructor(opts) {
        super(opts);
        console.log(this.view.width, this.view.height);
        this.board = new SudokuBoardGame(this.view.width, this.view.height);
        this.stage.addChild(this.board);
        this.stage.pivot.y = this.view.height;
        this.stage.scale.y = 1;
    }
    setBoard(board) {
        this.board.setBoard(board);
    }
}

export { SudokuBoardGame, SudokuBoardGameApp };
